    width: 50%;
} */
.wpb_add_button{
    background-color: #111;
    border: 0;
    color: #fff;
    padding: 10px 18px;
    border-radius: 3px;
    margin-top: 15px;
    cursor: pointer;
}

.anchor_group fieldset .container{
    background-color: #efefef;
    border-bottom: 1px solid #fff;
}

.anchor_group input{
    width: 100%;
}

.wpb_delete{
    position: absolute;
    z-index: 2;
    top: 15px;
    right: -35px;
    background: none;
    border: 0;
    transform: rotate(45deg);
    font-size: 2rem;
    color: #007cba;
    cursor: pointer;
}